import Util from './js/utils';

import Vue from 'vue';
import App from './App';
import {
  router
} from './router';

new Vue({
  router,
  el: '#app',
  render: h => h(App)
});

if (window.PLATFORM_CONFIG.debug) {
  // eslint-disable-next-line
  Util.loadScript("https://cdn.bootcss.com/vConsole/3.3.0/vconsole.min.js", () => new VConsole());
}