import Vue from 'vue';
import Router from 'vue-router';
import Util from './js/utils';

Vue.use(Router);

const routes = [{
    path: '*',
    redirect: '/login'
  },
  {
    name: 'login',
    component: () => import('./view/login'),
    meta: {
      title: '用户登录'
    }
  },
  {
    name: 'signup',
    component: () => import('./view/signup'),
    meta: {
      title: '用户注册'
    }
  },
  {
    name: 'fahui',
    component: () => import('./view/fahui'),
    meta: {
      title: '法会活动'
    }
  },
  {
    name: 'user',
    component: () => import('./view/user'),
    meta: {
      title: '会员中心'
    }
  },
  {
    name: 'cart',
    component: () => import('./view/cart'),
    meta: {
      title: '购物车'
    }
  },
  {
    name: 'work',
    component: () => import('./view/work'),
    meta: {
      title: '工作内容'
    }
  }
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({
  routes
});

function checkUserLogin() {
  let openid = localStorage['openid'];
  let user = Util.readLocal("user", window.PLATFORM_CONFIG.userExpire);
  if (user && openid == user.openid)
    return true;
  else
    return false;
}

router.beforeEach((to, from, next) => {
  if (to.name != "login" && to.name != "signup") {
    checkUserLogin() == false && next("login");
  } else {
    const title = to.meta && to.meta.title;
    if (title) {
      document.title = title;
    }
    next();
  }
});

export {
  router
};